import React, { useEffect, useState } from 'react';
import { Form, Grid } from 'semantic-ui-react';
import { Link } from 'react-router-dom';

import { API, showError } from '../helpers';

const WeChatSetting = () => {
  //  useState  inputs  setInputs 
  let [inputs, setInputs] = useState({
    WeChatToken: '', //   - WeChatToken:  Token
    WeChatAppID: '', //   - WeChatAppID:  AppID
    WeChatAppSecret: '', //   - WeChatAppSecret:  AppSecret
    WeChatEncodingAESKey: '', //   - WeChatEncodingAESKey:  EncodingAESKey
    WeChatOwnerID: '', //   - WeChatOwnerID:  OwnerID
    WeChatMenu: '', //   - WeChatMenu: 
  });

  //  useState  loading  setLoading  false
  let [loading, setLoading] = useState(false);

  // getOptions 
  //
  // 
  //   - 
  // 
  //   - 
  async function getOptions() {
    //  API  GET 
    const res = await API.get('/api/option');
    const { success, message, data } = res.data;
    if (success) {
      let newInputs = {};
      //  "WeChat"  newInputs 
      data.forEach((item) => {
        if (item.key.startsWith('WeChat')) {
          newInputs[item.key] = item.value;
        }
      });
      //  inputs  newInputs
      setInputs(newInputs);
    } else {
      // 
      showError(message);
    }
  }

  //  useEffect  getOptions 
  useEffect(() => {
    getOptions().then();
  }, []);

  // updateOption 
  //
  // 
  //   - key: 
  //   - value: 
  // 
  //   - 
  async function updateOption(key, value) {
    //  loading  true
    setLoading(true);

    //  API  PUT 
    const res = await API.put('/api/option', {
      key,
      value,
    });
    const { success, message } = res.data;
    if (success) {
      //  inputs  key  value
      setInputs((inputs) => ({ ...inputs, [key]: value }));
    } else {
      // 
      showError(message);
    }

    //  loading  false
    setLoading(false);
  }

  // handleInputChange 
  //
  // 
  //   - e: 
  //   - name: 
  //   - value: 
  // 
  //   - 
  async function handleInputChange(e, { name, value }) {
    if (name === 'WeChatMenu') {
      //  WeChatMenu inputs  value
      setInputs((inputs) => ({ ...inputs, [name]: value }));
    } else {
      //  updateOption 
      await updateOption(name, value);
    }
  }

  // submitWeChatMenu 
  //
  // 
  //   - 
  // 
  //   - 
  async function submitWeChatMenu() {
    //  updateOption  WeChatMenu 
    await updateOption('WeChatMenu', inputs.WeChatMenu);
  }

  return (
    <Grid columns={1}>
      <Grid.Column>
        <Form loading={loading}>
          <Form.Group widths="equal">
            <Form.Input
              label="Token"
              placeholder=""
              value={inputs.WeChatToken}
              name="WeChatToken"
              onChange={handleInputChange}
            />
          </Form.Group>
          <Form.Group widths="equal">
            <Form.Input
              label=" IDAppID"
              placeholder=""
              value={inputs.WeChatAppID}
              name="WeChatAppID"
              onChange={handleInputChange}
            />
          </Form.Group>
          <Form.Group widths="equal">
            <Form.Input
              label="AppSecret"
              placeholder=""
              value={inputs.WeChatAppSecret}
              name="WeChatAppSecret"
              onChange={handleInputChange}
            />
          </Form.Group>
          <Form.Group widths="equal">
            <Form.Input
              label="EncodingAESKey"
              placeholder=""
              value={inputs.WeChatEncodingAESKey}
              name="WeChatEncodingAESKey"
              onChange={handleInputChange}
            />
          </Form.Group>
          <Form.Group widths="equal">
            <Form.Input
              label="Root  ID"
              placeholder=""
              value={inputs.WeChatOwnerID}
              name="WeChatOwnerID"
              onChange={handleInputChange}
            />
          </Form.Group>
          <Form.Group widths="equal">
            <Form.TextArea
              label={
                <p>
                  
                  <a
                    target="_blank"
                    href="https://developers.weixin.qq.com/doc/offiaccount/Custom_Menus/Creating_Custom-Defined_Menu.html"
                  >
                    
                  </a>
                  
                </p>
              }
              placeholder="JSON "
              value={inputs.WeChatMenu}
              name="WeChatMenu"
              onChange={handleInputChange}
              style={{ minHeight: 150, fontFamily: 'JetBrains Mono, Consolas' }}
            />
          </Form.Group>
          <Form.Button onClick={submitWeChatMenu}></Form.Button>
        </Form>
      </Grid.Column>
    </Grid>
  );
};

export default WeChatSetting;
